      <!--   利润计算   -->
      <div class="fbm-lrjs-view" :class="{'fbm-lrjs-view-width': screenWidth <= fbmlimitWidth}">
        <div class="fbm-lrjs-title" :class="{'fbm-lrjs-title-top': screenWidth <= fbmlimitWidth}">利润计算</div>
        <div class="fbm-item-view">
