        </div>
        <div class="fbm-item-view">
          <div class="fbm-item-title">利润（单位：元）</div>
          <div class="fbm-item-value">{{ fbmlrjslrEvent() }}</div>
        </div>
      </div>
    </div>

    <!--  FBA  -->
    <h2 class="fba-title">FBA亚马逊物流产品利润计算器</h2>
    <div class="fba-view" :class="{'fba-view-column': screenWidth <= fbalimitWidth}">
      <!--   头程运费计算   -->
      <div class="fba-tcyfjs-view" :class="{'fba-tcyfjs-view-width': screenWidth <= fbalimitWidth}">
        <div class="fba-tcyfjs-title">头程运费计算</div>
        <div class="fba-item-view">
          <div class="fba-item-title">运费单价（单位：元/千克）</div>
          <input v-model="fbatcyfjsyfdj" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">产品重量（单位：千克）</div>
          <input v-model="fbatcyfjscpzl" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">头程运费（单位：元）</div>
          <div class="fba-item-value">{{ fbatcyfjstcyfEvent() }}</div>
        </div>
      </div>

      <!--   FBA费用计算   -->
      <div class="fba-fyjs-view" :class="{'fba-fyjs-view-width': screenWidth <= fbalimitWidth}">
        <div class="fba-fyjs-title" :class="{'fba-fyjs-title-top': screenWidth <= fbalimitWidth}">FBA费用计算</div>
        <div class="fba-item-view">
          <div class="fba-item-title">尾程配送费（单位：美元）</div>
          <input v-model="fbafbafyjswcpsf" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">合仓费（单位：美元）</div>
          <input v-model="fbafbafyjshcf" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">退货率（单位：%）</div>
          <input v-model="fbafbafyjsthl" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">类目佣金（单位：%）</div>
          <input v-model="fbafbafyjslmyj" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">尾程退货费（单位：美元）</div>
          <input v-model="fbafbafyjswcthf" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">平均退货费用（单位：元）</div>
          <div class="fba-item-value">{{ fbafbafyjspjthfyEvent() }}</div>
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">FBA总费用（单位：元）</div>
          <div class="fba-item-value">{{ fbafbafyjsfbazfyEvent() }}</div>
        </div>
      </div>

      <!--   成本计算   -->
      <div class="fba-cbjs-view" :class="{'fba-cbjs-view-width': screenWidth <= fbalimitWidth}">
        <div class="fba-cbjs-title" :class="{'fba-cbjs-title-top': screenWidth <= fbalimitWidth}">成本计算</div>
        <div class="fba-item-view">
          <div class="fba-item-title">采购/生产成本（单位：元）</div>
          <input v-model="fbacbjssccb" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">广告费用占比（单位：%）</div>
          <input v-model="fbacbjsggfyzb" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">VAT费用（单位：%）</div>
          <input v-model="fbacbjsvat" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">其他杂费（单位：%）</div>
          <div class="fba-item-value">{{ fbacbjsqtzf }}%</div>
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">总成本（单位：元）</div>
          <div class="fba-item-value">{{ fbacbjszcbEvent() }}</div>
        </div>
      </div>

      <!--   利润计算   -->
      <div class="fba-lrjs-view" :class="{'fba-lrjs-view-width': screenWidth <= fbalimitWidth}">
        <div class="fba-lrjs-title" :class="{'fba-lrjs-title-top': screenWidth <= fbalimitWidth}">利润计算</div>
        <div class="fba-item-view">
          <div class="fba-item-title">汇率（单位：1:X）</div>
          <input v-model="fbalrjshl" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">售价（单位：美金）</div>
          <input v-model="fbalrjssj" class="fba-item-input">
        </div>
        <div class="fba-item-view">
          <div class="fba-item-title">利润（单位：元）</div>
          <div class="fba-item-value">{{ fbalrjslrEvent() }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AccountOfProfit',
  props: {
    title: String
  },
  data() {
    return {
      fbmlimitWidth: 700,
      fbalimitWidth: 930,
      screenWidth: 0,

      // FBM-运费计算
      fbmyfjsyfdj: 120,
      fbmyfjscpzl: 0.04,
      fbmyfjsghf: 22,
      fbmyfjsgnyf: 0,
      fbmyfjsgjyf: 0,
      fbmyfjszyf: 0,

      // FBM-成本计算
      fbmcbjssccb: 80,
      fbmcbjslmyj: 15,
      fbmcbjsthl: 5,
      fbmcbjspjthfy: 6.11,
      fbmcbjsvat: 0,
      fbmcbjsqtzf: 1,
      fbmcbjszcb: 195.42,

      // FBM-利润计算
      fbmlrjshl: 6.447,
      fbmlrjssj: 79.99,
      fbmlrjslr: 320.27,

      // FBA-头程运费计算
      fbatcyfjsyfdj: 12,
      fbatcyfjscpzl: 0.8,
      fbatcyfjstcyf: 9.6,

      // FBA-FBA费用计算
      fbafbafyjswcpsf: 4.9,
      fbafbafyjshcf: 0.4,
      fbafbafyjsthl: 10,
      fbafbafyjslmyj: 15,
      fbafbafyjswcthf: 2.9,
      fbafbafyjspjthfy: 6.58,
      fbafbafyjsfbazfy: 59.44,

      // FBA-成本计算
      fbacbjssccb: 80,
      fbacbjsggfyzb: 15,
      fbacbjsvat: 0,
      fbacbjsqtzf: 1,
      fbacbjszcb: 290.21,

      // FBA-利润计算
      fbalrjshl: 6.447,
      fbalrjssj: 79.99,
      fbalrjslr: 225.49,
    }
  },
  created() {
    this.updateWindowDimensions()
    window.addEventListener('resize', this.updateWindowDimensions)
  },
  unmounted() {
    window.removeEventListener('resize', this.updateWindowDimensions)
  },
  methods: {
    updateWindowDimensions() {
      this.screenWidth = window.innerWidth
    },

    // FBM-运费计算-国际运费
    fbmyfjsgjyfEvent() {
      let self = this
      // 国际运费 = 运费单价 * 产品重量 + 挂号费
      self.fbmyfjsgjyf = parseFloat(self.fbmyfjsyfdj) * parseFloat(self.fbmyfjscpzl) + parseFloat(self.fbmyfjsghf)
      return self.fbmyfjsgjyf
    },

    // FBM-运费计算-总运费
    fbmyfjszyfEvent() {
      let self = this
      // 总运费 = 国内运费 + 国际运费
      self.fbmyfjszyf = parseFloat(self.fbmyfjsgnyf) + parseFloat(self.fbmyfjsgjyf)
      return self.fbmyfjszyf
    },

    // FBM-成本计算-平均退货费用
    fbmcbjspjthfyEvent() {
      let self = this
      // 平均退货费用 = (生产成本 + 总运费 + 售价 * 类目佣金 * 0.2 * 汇率) * 退货率
      self.fbmcbjspjthfy = (parseFloat(self.fbmcbjssccb) + parseFloat(self.fbmyfjszyf) + parseFloat(self.fbmlrjssj) * parseFloat(self.fbmcbjslmyj) * 0.01 * 0.2 * parseFloat(self.fbmlrjshl)) * parseFloat(self.fbmcbjsthl) * 0.01
      return self.fbmcbjspjthfy.toFixed(2)
    },

    // FBM-成本计算-总成本
    fbmcbjszcbEvent() {
      let self = this
      // 总成本 = 总运费 + 生产成本 + 平均退货费用 + 售价 * (类目佣金 + VAT费用 + 其他杂费) * 汇率
      self.fbmcbjszcb = parseFloat(self.fbmyfjszyf) + parseFloat(self.fbmcbjssccb) + parseFloat(self.fbmcbjspjthfy) + parseFloat(self.fbmlrjssj) * (parseFloat(self.fbmcbjslmyj) * 0.01 + parseFloat(self.fbmcbjsvat) * 0.01 + parseFloat(self.fbmcbjsqtzf) * 0.01) * parseFloat(self.fbmlrjshl)
      return self.fbmcbjszcb.toFixed(2)
    },

    // FBM-利润计算-利润
    fbmlrjslrEvent() {
      let self = this
      // 利润 = 售价 * (1 - 类目佣金 - VAT费用 - 其他杂费) * 汇率 - 总运费 - 生产成本 - 平均退货费用
      self.fbmlrjslr = parseFloat(self.fbmlrjssj) * (1 - parseFloat(self.fbmcbjslmyj) * 0.01 - parseFloat(self.fbmcbjsvat) * 0.01 - parseFloat(self.fbmcbjsqtzf) * 0.01) * parseFloat(self.fbmlrjshl) - parseFloat(self.fbmyfjszyf) - parseFloat(self.fbmcbjssccb) - parseFloat(self.fbmcbjspjthfy)
      return self.fbmlrjslr.toFixed(2)
    },

    // FBA-头程运费计算-头程运费
    fbatcyfjstcyfEvent() {
      let self = this
      // 头程运费 = 运费单价 * 产品重量
      self.fbatcyfjstcyf = parseFloat(self.fbatcyfjsyfdj) * parseFloat(self.fbatcyfjscpzl)
      return self.fbatcyfjstcyf.toFixed(2)
    },

    // FBA-FBA费用计算-平均退货费用
    fbafbafyjspjthfyEvent() {
      let self = this
      // 平均退货费用 = (尾程配送费 + 尾程退货费 + 售价 * 类目佣金 * 20%) * 汇率 * 退货率
      self.fbafbafyjspjthfy = (parseFloat(self.fbafbafyjswcpsf) + parseFloat(self.fbafbafyjswcthf) + parseFloat(self.fbalrjssj) * parseFloat(self.fbafbafyjslmyj) * 0.01 * 0.2) * parseFloat(self.fbalrjshl) * parseFloat(self.fbafbafyjsthl) * 0.01
      return self.fbafbafyjspjthfy.toFixed(2)
    },

    // FBA-FBA费用计算-FBA总费用
    fbafbafyjsfbazfyEvent() {
      let self = this
      // FBA总费用 = (尾程配送费 + 合仓费 + 尾程退货费) * 汇率 + 平均退货费用
      self.fbafbafyjsfbazfy = (parseFloat(self.fbafbafyjswcpsf) + parseFloat(self.fbafbafyjshcf) +parseFloat(self.fbafbafyjswcthf)) * parseFloat(self.fbalrjshl) + parseFloat(self.fbafbafyjspjthfy)
      return self.fbafbafyjsfbazfy.toFixed(2)
    },

    // FBA-成本计算-总成本
    fbacbjszcbEvent() {
      let self = this
      // 总成本 = (售价 * (类目佣金 + VAT费用 + 其他杂费 + 广告费用占比) + 尾程配送费 + 合仓费) * 汇率 + 生产成本 + 平均退货费用 + 头程运费
      self.fbacbjszcb = (parseFloat(self.fbalrjssj) * (parseFloat(self.fbafbafyjslmyj) * 0.01 + parseFloat(self.fbacbjsvat) * 0.01 + parseFloat(self.fbacbjsqtzf) * 0.01 + parseFloat(self.fbacbjsggfyzb) * 0.01) + parseFloat(self.fbafbafyjswcpsf) + parseFloat(self.fbafbafyjshcf)) * parseFloat(self.fbalrjshl) + parseFloat(self.fbacbjssccb) + parseFloat(self.fbafbafyjspjthfy) + parseFloat(self.fbatcyfjstcyf)
      return self.fbacbjszcb.toFixed(2)
    },

    // FBA-利润计算-利润
    fbalrjslrEvent() {
      let self = this
      // 利润 = (售价 * (1 - 类目佣金 - VAT费用 - 其他杂费 - 广告费用占比) - 尾程配送费 - 合仓费) * 汇率 - 生产成本 - 平均退货费用 - 头程运费
      self.fbalrjslr = (parseFloat(self.fbalrjssj) * (1 - parseFloat(self.fbafbafyjslmyj) * 0.01 - parseFloat(self.fbacbjsvat) * 0.01 - parseFloat(self.fbacbjsqtzf) * 0.01 - parseFloat(self.fbacbjsggfyzb) * 0.01) - parseFloat(self.fbafbafyjswcpsf) - parseFloat(self.fbafbafyjshcf)) * parseFloat(self.fbalrjshl) - parseFloat(self.fbacbjssccb) - parseFloat(self.fbafbafyjspjthfy) - parseFloat(self.fbatcyfjstcyf)
      return self.fbalrjslr.toFixed(2)
    },
  }
}
</script>

